.home-bg{
    width: 100%;
    background: url("../Assets/pc/378152744_ebb5daed-d670-4575-a939-cb638c65ca11.png");
    background-position: center center;
    background-size: cover;
    background-repeat: no-repeat;
}



@keyframes pling-effect {
    0% {
        opacity: 0.3; /* Start hidden */
    }
    50% {
        opacity: 1; /* Fully visible */
    }
    100% {
        opacity: 0.3; /* Hidden again */
    }
}
.gold-paper{
    animation: pling-effect 3s ease-in-out infinite;
    opacity: 1;
}

